/* Form layout */
:root {
  --ct-form-layout-columns: 2;
}
.ct-form-layout {
  display: grid;
  grid-gap: 0 10px;
  grid-template-columns: repeat(var(--ct-form-layout-columns), max-content 1fr);
  place-items: baseline stretch;
}
.ct-form-layout > hr {
  border: none;
  grid-column: 1 / -1;
  height: 15px;
  margin: 0;
  padding: 0;
}
.ct-form-layout > label,
.ct-form-layout > [role="group"] > label,
.ct-form-layout .checkbox > label,
.ct-form-layout .radio > label,
.ct-form-layout .checkbox-inline,
.ct-form-layout .radio-inline {
  line-height: 26px;
}
.ct-form-layout > .control-label {
  align-self: flex-start;
  padding-left: 10px;
  text-align: right;
}
.ct-form-layout > div,
.ct-form-layout > span,
.ct-form-layout > a,
.ct-form-layout > p,
.ct-form-layout > dl,
.ct-form-layout > ol,
.ct-form-layout > ul,
.ct-form-layout > fieldset,
.ct-form-layout > input,
.ct-form-layout > textarea,
.ct-form-layout > samp,
.ct-form-layout > pre,
.ct-form-layout > code,
.ct-form-layout > kbd,
.ct-form-layout > label:not(.control-label) {
  grid-column: 2 / -1;
  line-height: 26px;
  margin: 0;
}
.ct-form-layout > div:not(.ct-form-layout-relax),
.ct-form-layout > span:not(.ct-form-layout-relax),
.ct-form-layout > a:not(.ct-form-layout-relax),
.ct-form-layout > p:not(.ct-form-layout-relax),
.ct-form-layout > dl:not(.ct-form-layout-relax),
.ct-form-layout > ol:not(.ct-form-layout-relax),
.ct-form-layout > ul:not(.ct-form-layout-relax),
.ct-form-layout > fieldset:not(.ct-form-layout-relax),
.ct-form-layout > input:not(.ct-form-layout-relax),
.ct-form-layout > textarea:not(.ct-form-layout-relax),
.ct-form-layout > samp:not(.ct-form-layout-relax),
.ct-form-layout > pre:not(.ct-form-layout-relax),
.ct-form-layout > code:not(.ct-form-layout-relax),
.ct-form-layout > kbd:not(.ct-form-layout-relax),
.ct-form-layout > label:not(.control-label):not(.ct-form-layout-relax) {
  width: auto;
}
.ct-form-layout > input[size] {
  justify-self: start;
}
.ct-form-layout > .bootstrap-select:not(.ct-form-layout-relax),
.ct-form-layout > .btn-group:not(.ct-form-layout-relax),
.ct-form-layout > .dropdown:not(.ct-form-layout-relax) {
  width: auto !important;
}
.ct-form-layout > [role=group] {
  align-self: start;
  display: grid;
  grid-gap: 5px;
  grid-auto-flow: column;
  grid-template-columns: max-content;
  justify-content: start;
}
.ct-form-layout > [role=group]:not(.ct-form-layout-relax) > .dropdown {
  width: auto !important;
}
.ct-form-layout > [role=group] > .form-control {
  width: auto;
}
.ct-form-layout .checkbox > label,
.ct-form-layout .radio > label,
.ct-form-layout .checkbox-inline,
.ct-form-layout .radio-inline {
  display: inline-flex;
  padding-left: 0;
  padding-right: 10px;
}
.ct-form-layout .checkbox > label > input[type="checkbox"],
.ct-form-layout .radio > label > input[type="checkbox"],
.ct-form-layout .checkbox-inline > input[type="checkbox"],
.ct-form-layout .radio-inline > input[type="checkbox"],
.ct-form-layout .checkbox > label > input[type="radio"],
.ct-form-layout .radio > label > input[type="radio"],
.ct-form-layout .checkbox-inline > input[type="radio"],
.ct-form-layout .radio-inline > input[type="radio"] {
  height: 16px;
  margin: 5px 0;
  margin-right: 10px;
  position: static;
}
.ct-form-layout fieldset > .checkbox:first-child,
.ct-form-layout fieldset > .radio:first-child {
  margin-top: 0;
}
.ct-form-layout fieldset > .checkbox:last-child,
.ct-form-layout fieldset > .radio:last-child {
  margin-bottom: 0;
}
.ct-form-layout .list-group {
  margin-bottom: 5px;
}
.ct-form-layout > .ct-form-layout-split {
  grid-column: auto / auto;
}
.ct-form-layout-stretch {
  justify-content: stretch !important;
}
.ct-form-layout-relax {
  justify-self: start;
}
.modal-dialog.modal-sm .ct-form-layout > .ct-form-layout-split {
  grid-column: 2 / -1;
}
@media (max-width: 640px) {
  :root {
    --ct-form-layout-columns: 1;
  }
}
@media (max-width: 480px) {
  .ct-form-layout {
    grid-template-columns: initial;
  }
  .ct-form-layout > * {
    grid-column: auto;
    line-height: 1.66666667;
    max-width: 100%;
  }
  .ct-form-layout > .control-label {
    padding: 0;
    text-align: left;
  }
  .ct-form-layout > .control-label:not(:first-child) {
    margin: 6px 0 0;
  }
  .ct-form-layout > hr {
    height: 12px;
  }
}
